import { Component, OnInit, Input, OnChanges, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from 'src/app/core/Servers/authentication.service';

@Component({
  selector: 'app-chi-tiet-rap',
  templateUrl: './chi-tiet-rap.component.html',
  styleUrls: ['./chi-tiet-rap.component.scss'],
})
export class ChiTietRapComponent implements OnInit, OnChanges {
  @Input() isTheme;
  @Input() chiTietRap: any[] = []; // H thng rp chiu theo phim *1
  @Input() cumRamChieu: any[] = []; // Ly Cm Rp u tin  Active *2
  @Input() chiTietPhim; // Chi tit phim ( ly hnh nh) *3
  lichChieu: any[] = []; // Lch chiu phim (gm ngy v gi) *4
  ngayChieu: any[] = []; // tch ngy chiu t lch chiu *5
  gioChieu: any[] = []; // tch gi chiu t lch chiu *6
  maRap: string = ''; //  thay i mu font khi rp # nhau *7
  indexActive: number = 0; //  active rp u tin *8
  indexActiveCumRap: number = 0; //  active Cm rp u tin *9
  ngayActive: number = 0;
  currentUser: any = {};
  public currentWindowWidth: number;
  @HostListener('window:resize')
  onResize() {
    this.currentWindowWidth = window.innerWidth;
  }
  changeIndex(index) {
    // Active icon rp khi click vo
    this.indexActive = index;
    console.log(this.indexActive)
  }
  changeIndexCumRap(index) {
    // Active icon Cm rp khi click vo
    this.indexActiveCumRap = index;
    console.log(this.indexActive)
  }
  changeNgayActive(value) {
    this.ngayActive = value;
  }
  changeRapChieu(value) {
    // Thay i cc thng s khi thay i rp
    this.chiTietRap.forEach((rapItem) => {
      // lc trong h thng rp
      if (rapItem.maHeThongRap === value) {
        //Xt iu kin, value l m h thng rp
        this.lichChieu = []; //Reset li mng lch chiu khi click v Ngy chiu v gi chiu dng push
        this.maRap = rapItem.maHeThongRap; // Thay i m rp *9
        this.cumRamChieu = rapItem.cumRapChieu; // Thay i mng cm rp chiu theo rp
        this.lichChieu = this.cumRamChieu[0].lichChieuPhim; // Lch chiu hin th phn t u tin ca mng
        this.setNgayChieu(); // Set li ngy v gi chiu
      }
    });
  }
  changeLichChieu(value) {
    //Thay i thng s khi thay i cm rp
    this.cumRamChieu.forEach((cumItem) => {
      // lc trong h thng cm rp
      if (cumItem.maCumRap === value) {
        // Xt iu kin, value l m cm rp
        this.lichChieu = []; //Reset li mng lch chiu khi click v Ngy chiu v gi chiu dng push
        this.lichChieu = cumItem.lichChieuPhim; //Gn mng mi c lch chiu theo cm rp
        this.setNgayChieu(); // Set li ngy v gi chiu
      }
    });
  }
  changeTime(value) {
    //Thay i thng s khi thay i Ngy chiu
    this.setGioChieu(value); // Set li gi chiu khi i ngy
  }
  // 2 hm set li ngy v gi chiu
  setNgayChieu() {
    this.ngayChieu = []; //Set ngy chiu v rng
    for (let i = 0; i < this.lichChieu.length; i++) {
      this.ngayChieu.push(this.lichChieu[i].ngayChieuGioChieu.split('T')[0]); //push vo mng ngayChieu ngy chiu t lch chiu
    }
    this.ngayChieu = this.ngayChieu.filter(
      (item, index) => this.ngayChieu.indexOf(item) == index // Xa phn t nhng ngy chiu lp nhau
    );
    this.indexActiveCumRap = 0;
    this.ngayActive = 0;
    this.setGioChieu(this.ngayChieu[0]); // Set li gi chiu hin th l ngy chiu u tin trong mng
  }
  setGioChieu(value) {
    this.gioChieu = this.lichChieu.filter(
      (item) => item.ngayChieuGioChieu.split('T')[0] == value // Lc gi chiu theo ngy
    );
    console.log(this.gioChieu);
  }
  datVe(value) {
    if (this.currentUser.taiKhoan) {
      this.router.navigate([`/datve/${value}`]);
    }
  }
  constructor(private auth: AuthenticationService, private router: Router) {}
  ngOnInit(): void {
    this.currentWindowWidth = window.innerWidth;
    this.maRap = this.chiTietRap[0].maHeThongRap; // Chn M rp in th ln u
    this.lichChieu = this.cumRamChieu[0].lichChieuPhim; // Lch chiu in th ln u
    this.setNgayChieu(); // Set ngy gi chiu hin th ln u
    this.auth.currentUser.subscribe({
      next: (result) => {
        this.currentUser = result;
        console.log(this.currentUser)
      },
    });
  }
  ngOnChanges() {}
}
