import { Component, Input, OnChanges, OnInit } from '@angular/core';

@Component({
  selector: 'app-modal-warning',
  templateUrl: './modal-warning.component.html',
  styleUrls: ['./modal-warning.component.scss'],
})
export class ModalWarningComponent implements OnInit, OnChanges {
  @Input() warning: string;
  thongBao: string = 'Khng th  trng gh  gia!';
  constructor() {}
  currentWidth:number
  reload() {
    if (this.warning == 'timer') {
      location.reload();
    }
    return;
  }
  ngOnChanges() {
    switch (this.warning) {
      case 'muaVe': {
        this.thongBao = 'Vui Lng ng Nhp  Mua V!';
        break;
      }
      case 'danhGia': {
        this.thongBao = 'Vui Lng ng Nhp  nh Gi Phim!';
        break;
      }
      case 'timer': {
        this.thongBao = ' ht thi gian gi gh!';
        break;
      }
      case 'chonPhim': {
        this.thongBao = 'Vui Lng Chn Phim v Gi Chiu!';
        break;
      }
      default:
        break;
    }
    console.log(this.warning)
    console.log(this.thongBao)
  }
  ngOnInit(): void {
    this.currentWidth = window.innerWidth
    console.log(this.warning)
  }
}
